import React from 'react';

import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import HomeLayout from './HomeLayout';

const backgroundImage = '/products/product1.png';

const index = () => {
  return (
    <HomeLayout
    sxBackground={{
      backgroundImage: `url(${backgroundImage})`,
      backgroundColor: '#7fc7d9', // Average color of the background image.
      backgroundPosition: 'center',
    }}>

      <Typography color="inherit" align="center" variant="h2" marked="center">
        Merengn Doble Fresa
      </Typography>
      <Typography
        color="inherit"
        align="center"
        variant="h5"
        sx={{ mb: 4, mt: { sx: 4, sm: 10 } }}
      >
        Te encantan las fresas? Prueba el exquisito Merengn con doble capa de merengue, doble capa de crema de la casa, Fresas fresca y salsa de tu eleccin.
      </Typography>
      <Button
      color="primary"
      variant="contained"
      size="large"
      component="a"
      href="/productos"
      sx={{ minWidth: 200 }}>
          <Typography color="white">Ver productos</Typography>
      </Button>
    </HomeLayout>

  );
}

export default index;